<template>
  <div name="tours">
    <CoverImage
      page="Tours"
      backgroundImage="backgnd_5.jpg"
      title="Tours"
      subTitle="Explore great places to stay, eat, shop or visit!"
    />

    <!-- Tour destinations -->
    <v-container>
      <p class="display-0 text--disabled">
        <br />
        <br />Tour Destinations
      </p>
      <h2>
        <strong>Places</strong>
        <font class="font-weight-light">&nbsp; we take you!</font>
      </h2>

      <br />
      <v-layout row wrap justify-space-around class="ma-2 mp-2">
        <v-flex sm10 md5 data-aos="fade-right">
          <v-expansion-panels tile multiple v-model="destinationsArray_domestic">
            <v-expansion-panel>
              <v-expansion-panel-header>
                <h3 style="font-weight:bold;">Destinations in India (Domestic)</h3>
                <template v-slot:actions>
                  <v-icon color="success">mdi-arrow-down-drop-circle-outline</v-icon>
                </template>
              </v-expansion-panel-header>
              <v-expansion-panel-content>
                <ul>
                  <li
                    v-for="(destination, i) in tourDestinations_domestic"
                    :key="i"
                    style="font-size: 1.2em; font-weight: 2;"
                  >{{destination.location}}</li>
                </ul>
              </v-expansion-panel-content>
            </v-expansion-panel>
          </v-expansion-panels>
        </v-flex>
        <v-flex sm10 md5 data-aos="fade-left">
          <v-expansion-panels tile multiple v-model="destinationsArray_international">
            <v-expansion-panel>
              <v-expansion-panel-header>
                <h3 style="font-weight:bold;">International Destinations</h3>
                <template v-slot:actions>
                  <v-icon color="success">mdi-arrow-down-drop-circle-outline</v-icon>
                </template>
              </v-expansion-panel-header>
              <v-expansion-panel-content>
                <v-expansion-panels
                  tile
                  multiple
                  v-for="(destination, i) in tourDestinations_international"
                  :key="i"
                >
                  <v-expansion-panel>
                    <v-expansion-panel-header
                      v-if="hasChildren(destination)"
                      style="font-size: 1.2em; font-weight: bold;"
                    >
                      {{destination.location}}
                      <template v-slot:actions>
                        <v-icon color="success">mdi-arrow-down-drop-circle-outline</v-icon>
                      </template>
                    </v-expansion-panel-header>
                    <v-expansion-panel-header
                      v-if="!hasChildren(destination)"
                      style="font-size: 1.2em; font-weight: bold;"
                      expand-icon="invalid-icon"
                      disabled
                    >{{destination.location}}</v-expansion-panel-header>
                    <v-expansion-panel-content>
                      <ul>
                        <li
                          v-for="(dest,j) in destination.children"
                          :key="j"
                          style="font-size: 1.2em; font-weight: 2;"
                        >{{ dest }}</li>
                      </ul>
                    </v-expansion-panel-content>
                  </v-expansion-panel>
                </v-expansion-panels>
              </v-expansion-panel-content>
            </v-expansion-panel>
          </v-expansion-panels>
        </v-flex>
      </v-layout>
      <br />
    </v-container>

    <!-- Types of tours -->
    <v-container fluid class="light-blue lighten-5">
      <v-container>
        <p class="display-0 text--disabled">
          <br />Types of Tours
        </p>
        <h2>
          <strong>Types of Tours</strong>
          <font class="font-weight-light">&nbsp; we offer</font>
        </h2>

        <v-layout row wrap class="ma-2 pt-4">
          <v-flex xs12 md3 v-for="(tour, i) in typesOfTours" :key="i">
            <v-card aspect-ratio="1" class="ma-2" hover>
              <v-img :src="require('@/assets/images/tours/' + tour.imageName)"></v-img>
              <v-card-title class="font-weight-regular">{{ tour.type }}</v-card-title>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-container>

    <!-- Standard tours -->
    <v-container>
      <p class="display-0 text--disabled">
        <br />Standard Tours
      </p>
      <h2>
        <strong>Our</strong>
        <font class="font-weight-light">&nbsp; Standard Tours</font>
      </h2>

      <v-layout row wrap class="ma-2 pt-4">
        <v-flex sm12 md12>
          <v-expansion-panels tile multiple focusable data-aos="zoom-in-up" data-aos-duration="800">
            <v-expansion-panel v-for="(tour, i) in standardTours" :key="i">
              <v-expansion-panel-header class="font-weight-bold">
                {{ tour.title }}
                <template v-slot:actions>
                  <v-icon color="warning">mdi-arrow-down-drop-circle-outline</v-icon>
                </template>
              </v-expansion-panel-header>
              <v-expansion-panel-content>
                <br />
                <v-layout row wrap align="end">
                  <v-flex sm12 md6 class="display-1 font-italic">
                    <br />
                    {{tour.subtitle}}
                  </v-flex>
                  <v-flex sm12 md6 class="display-1 text-end">
                    <p class="text--disabled" style="font-size:18px;">Discount price</p>
                    {{tour.price}}
                  </v-flex>
                </v-layout>
                <br />
                <v-timeline :dense="$vuetify.breakpoint.smAndDown">
                  <v-timeline-item
                    v-for="(day, j) in tour.days"
                    :key="j"
                    :color="getTimelineColor(j) + ' lighten-2'"
                    fill-dot
                    :right="j%2===0"
                    :small="j%2===1"
