                          size="32"
                          class="mr-4"
                        >mdi-home-outline</v-icon>
                        <v-icon v-else dark size="32" class="mr-4">mdi-earth</v-icon>
                        <h2 class="display-0 white--text font-weight-light">{{day.title}}</h2>
                      </v-card-title>
                      <v-card-text class="ma-2">{{day.description}}</v-card-text>
                    </v-card>
                  </v-timeline-item>
                </v-timeline>
                <font class="text--primary">
                  <h3 class="font-weight-bold">Notes:</h3>
                  <ul style="font-size:15px;">
                    <li v-for="(note, j) in tour.notes" :key="j">{{note}}</li>
                  </ul>
                </font>
              </v-expansion-panel-content>
            </v-expansion-panel>
          </v-expansion-panels>
        </v-flex>
      </v-layout>
