          subtitle: "Thailand: Phuket 3N, Krabi 2N, Bangkok 1N",
          price: "₹ 35,000/-",
          days: [
            {
              title: "Day 1: Phuket",
              description:
                "Welcome to Thailand. Arrive at Phuket and transfer to the hotel. At Evening proceed for Phuket Fantasea show which is a Las Vegas style Thai cultural show infused with cutting edge technology and state-of-the-art show elements. Overnight at Phuket."
