              title: "Day 2: Phuket - James Bond Island",
              description:
                'Today we visit Phang Nga Bay National park for scenic views of literally hundreds of limestone cliffs rising over the sea, tour the movie scene "James Bond Island".   Overnight at Phuket.'
            },
