              title: "Day 3: Phuket",
              description:
                "Today after breakfast you have entire day at leisure to explore Phuket on own or join our tours and Visit Trickeye Museum and Immerse yourself in different settings at 6 themed zones and recreate into memorable photographs with your imagination. Overnight at Phuket."
            },
            {
              title: "Day 4: Phuket - Phi Phi Island - Krabi",
              description:
                "Today proceed for Phi Phi Island Tour incl. lunch by big boat. Cruise by big boat to visit one of Asia’s most beautiful islands. Upon arrival to Phi Phi Don, the larger of the two Phi Phi Islands, time is at leisure for swimming and snorkeling among the colorful corals and then transfer to Krabi. Overnight at Krabi.\r\n\r\n \
                Note: Entrance fees to the national park at Phi Phi Island to be paid directly by the guest 400 THB per person."
            },
            {
              title: "Day 5: Krabi - 4 Island",
              description:
                "After breakfast, have a fun filled day on the Four Island Tour visiting hra Nang Cave, Tup Island, Chicken Island and Poda Island. Evening is at leisure. Overnight at Krabi."
            },
            {
              title: "Day 6: Krabi - Bangkok",
              description:
                "After Breakfast, we leave for Bangkok. In the evening enjoy shopping at Indra Square/ Pratunam market."
            },
            {
              title: "Day 7: Bangkok - Hometown",
              description:
                "After breakfast proceed to Airport to board flight to your hometown or onward destination Holiday Concludes. Let’s stay in touch on Facebook/email and meet again on another memorable Holiday. See you soon!"
            }
          ],
          notes: [
            "The above cost (Ex-Thailand) is approximate and subject to change according to your customizations.",
            "Thai law requires tourists to arrive with a minimum of 10,000 Baht (~300 USD) per person or 20,000 Baht per family (~600 USD)."
          ]
