              title: "Day 4: Phuket - Phi Phi Island - Krabi",
              description:
                "Today proceed for Phi Phi Island Tour incl. lunch by big boat. Cruise by big boat to visit one of Asia’s most beautiful islands. Upon arrival to Phi Phi Don, the larger of the two Phi Phi Islands, time is at leisure for swimming and snorkeling among the colorful corals and then transfer to Krabi. Overnight at Krabi."
            },
