          ]
        }
      ]
    };
  },
  created() {
    setInterval(() => {
      if (!this.autorunTestimonials) return;
      if (++this.windowTestimonials >= this.testimonials.length)
        this.windowTestimonials = 0;
    }, 4000);
  },
  methods: {
    hasChildren: function(obj) {
      if (obj != null && Array.isArray(Object.keys(obj))) {
        return Object.keys(obj).includes("children");
      } else {
        return false;
      }
    },
    getTimelineColor(i) {
      let arrayColors = [
        "purple",
        "cyan",
        "red",
        "green",
        "brown",
        "pink",
        "blue-grey"
      ];
      return arrayColors[i % arrayColors.length];
    }
  },
  computed: {
    destinationsArray_domestic() {
      return [...Array(this.tourDestinations_domestic).keys()].map((k, i) => i);
    },
    destinationsArray_international() {
      return [...Array(this.tourDestinations_international).keys()].map(
        (k, i) => i
      );
    }
  }
};
</script>

<style scoped>
.v-expansion-panels {
  z-index: 0;
}
.v-timeline-item__inner-dot {
  color: white;
  font-weight: bold;
}
</style>


